import Bugsnag from '@bugsnag/js'
import BugsnagPluginReact from '@bugsnag/plugin-react'

export const ENVIRONMENTS = ['production', 'staging', 'test', 'development']

export function isNullOrUndefined(param) {
    return typeof param === 'undefined' || param === null;
}

export function getEnv() {
    console.log(process.env.IS_BUILDING)
    let env = "none"
    if (process.env.IS_BUILDING === true) {
        env = "build"
    } else if (!isNullOrUndefined(process.env.GATSBY_ENV)) {
        env = process.env.GATSBY_ENV.trim();
    } else {
        env = process.env.NODE_ENV.trim();
    }
    return env
}

export function isProduction() {
    return getEnv() === 'production'
}

Bugsnag.start({
    apiKey: '6251e9dc9ab73d2cd7d6818c6b71816c',
    plugins: [new BugsnagPluginReact()],
    releaseStage: getEnv(),
    enabledReleaseStages: ['production', 'staging', 'development']
})